<template>
  <div class="phoneContainer">
    <div class="modal-overlay" v-if="ModalW == 1">
      <div class="modal-content">
        <img class="imgblk3" src="../assets/img3.jpeg" @click="ModalW = 0" />
      </div>
    </div>
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="[goBack(), stopSpeech()]"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
      <h2 class="word1">2024년 10월 17일</h2>
    </div>
    <div class="imgDisplayed">
      <img class="imgblk2" src="../assets/img3.jpeg" @click="ModalW = 1" />
    </div>
    <div class="infDiv">
      <h1 class="festD" :style="{ fontSize: fontSz + 2 + 'px' }">
        지로영수증 (은행 수납용)
      </h1>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>날짜:</b> 2015.01.01 ~ 2015.01.22
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>장소:</b> 경주시 석장동 동대로
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>가격:</b> 59940원
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>시간:</b> 시간 정보 없음
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>요약:</b> 2015년 1월에 사용한 도시가스 요금 청구서로, 경주시 석장동
        동대로에 거주하는 홍길동 고객의 청구 금액은 59,940원입니다. 사용 기간은
        12월 1일부터 12월 31일까지로, 검침량은 53,681이며, 사용 열량은
        2,276.2478입니다.
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>기타:</b> 경주시에 거주하는 홍길동 고객의 도시가스 요금 청구서이며,
        2015년 1월 22일까지 결제해야 하는 내용이 포함되어 있습니다
      </p>
      <div class="buttonGroup1">
        <button class="shortBtn1" @click="upFontSize">+ 확대</button>
        <button class="shortBtn1" @click="downFontSize">- 축소</button>
      </div>
    </div>
    <div class="btnBundles2">
      <button class="mainBtn b" @click="speakText">
        <img class="mainIcon" src="../assets/재생.png" />
        <span class="mainTxt">재생</span>
      </button>
      <button class="mainBtn b" @click="stopSpeech">
        <img class="mainIcon" src="../assets/정지.png" />
        <span class="mainTxt">정지</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "GallerySummary3",
  components: {},
  data() {
    return {
      currentTime: null,
      fontSz: 16,
      ModalW: 0,
      volume: 1.0, // 기본 볼륨
      rate: 1.0, // 기본 속도
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    upFontSize() {
      this.fontSz += 1; // 폰트 크기를 1px 증가
    },
    downFontSize() {
      if (this.fontSz > 1) {
        this.fontSz -= 1; // 폰트 크기를 1px 감소, 최소 1px로 설정
      }
    },
    speakText() {
      const text = `
      지로영수증 (은행 수납용),
      날짜: 2015.01.01 ~ 2015.01.22,
      장소: 경주시 석장동 동대로,
      가격: 59940원,
      시간: 시간 정보 없음,
      요약: 2015년 1월에 사용한 도시가스 요금 청구서로, 경주시 석장동 동대로에 거주하는 홍길동 고객의 청구 금액은 59,940원입니다. 사용 기간은 12월 1일부터 12월 31일까지로, 검침량은 53,681이며, 사용 열량은 2,276.2478입니다.,
      기타: 경주시에 거주하는 홍길동 고객의 도시가스 요금 청구서이며, 2015년 1월 22일까지 결제해야 하는 내용이 포함되어 있습니다. 
      `;
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.volume = this.volume; // 볼륨 조절
      utterance.rate = this.rate; // 속도 조절
      utterance.lang = "ko-KR"; // 한국어로 설정
      speechSynthesis.speak(utterance);
    },
    stopSpeech() {
      speechSynthesis.cancel(); // 현재 진행 중인 음성을 멈춤
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
