<template>
  <div class="phoneContainer">
    <div class="withBack">
      <RouterLink to="/main">
        <img
          role="button"
          tabindex="0"
          alt=""
          class="backImg"
          src="../assets/뒤로가기.png"
        />
      </RouterLink>
    </div>
    <div class="calendar-container">
      <div class="calendar-header">
        <button @click="previousMonth" class="dayBtn">이전</button>
        <span
          :style="{ color: '#1f4e79', fontSize: '22px', fontWeight: 'bold' }"
          >{{ formattedCurrentMonth }}</span
        >
        <button @click="nextMonth" class="dayBtn">이후</button>
      </div>
      <div class="calendar-days">
        <div class="day-name" v-for="day in dayNames" :key="day">
          {{ day }}
        </div>
        <div
          v-for="(date, index) in calendarDays"
          :key="index"
          class="calendar-date"
          :class="{
            'is-today': isToday(date),
            'is-selected':
              selectedDate && date && date.getTime() === selectedDate.getTime(),
            'has-note': date && hasNote(date), // 메모가 있는 날짜에 클래스 추가
          }"
          @click="selectDate(date)"
        >
          <span v-if="date">{{ date.getDate() }}</span>
        </div>
      </div>
    </div>
    <!-- 메모장 영역 -->
    <div v-if="selectedDate" class="note-section">
      <p :style="{ color: '#1f4e79', fontSize: '18px', fontWeight: 'bold' }">
        {{ formattedSelectedDate }}의 일정
      </p>
      <textarea v-model="note" placeholder="일정을 등록해주세요!"></textarea>
      <button @click="saveNote" class="dayBtn">저장</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "CalanderPage",
  components: {},
  data() {
    return {
      currentDate: new Date(),
      selectedDate: null,
      note: "",
      notes: {
        "Sun Oct 20 2024":
          "- 독감 무료 예방접종\n- 안양2동 복지센터 폐기물 처리 안내",
        "Wed Oct 16 2024": "- 평촌대로 254번길 배수로 공사",
        "Sat Nov 16 2024": "- 전국 노래자랑 안양편",
      }, // 날짜별 메모를 저장하는 객체
    };
  },
  computed: {
    formattedCurrentMonth() {
      return `${this.currentDate.getFullYear()}년 ${
        this.currentDate.getMonth() + 1
      }월`;
    },
    formattedSelectedDate() {
      if (!this.selectedDate) return "";
      return `${this.selectedDate.getFullYear()}년 ${
        this.selectedDate.getMonth() + 1
      }월 ${this.selectedDate.getDate()}일`;
    },
    dayNames() {
      return ["일", "월", "화", "수", "목", "금", "토"];
    },
    calendarDays() {
      const startOfMonth = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth(),
        1
      );
      const endOfMonth = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth() + 1,
        0
      );
      const days = [];

      // 시작 요일 전의 빈 칸 채우기
      for (let i = 0; i < startOfMonth.getDay(); i++) {
        days.push(null);
      }

      // 해당 월의 날짜 추가
      for (let i = 1; i <= endOfMonth.getDate(); i++) {
        days.push(
          new Date(
            this.currentDate.getFullYear(),
            this.currentDate.getMonth(),
            i
          )
        );
      }

      return days;
    },
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    previousMonth() {
      this.currentDate.setMonth(this.currentDate.getMonth() - 1);
      this.currentDate = new Date(this.currentDate); // 데이터 갱신을 위해 새 Date 객체로 재할당
    },
    nextMonth() {
      this.currentDate.setMonth(this.currentDate.getMonth() + 1);
      this.currentDate = new Date(this.currentDate); // 데이터 갱신을 위해 새 Date 객체로 재할당
    },
    isToday(date) {
      if (!date) return false;
      const today = new Date();
      return (
        date.getDate() === today.getDate() &&
        date.getMonth() === today.getMonth() &&
        date.getFullYear() === today.getFullYear()
      );
    },
    selectDate(date) {
      this.selectedDate = date;
      this.note = this.notes[date.toDateString()] || ""; // 날짜에 저장된 메모 불러오기
    },
    saveNote() {
      this.notes[this.selectedDate.toDateString()] = this.note; // 메모 저장
      alert(`${this.formattedSelectedDate}의 일정이 저장되었습니다.`);
    },
    hasNote(date) {
      return !!this.notes[date.toDateString()]; // 메모가 있는지 확인
    },
  },
};
</script>

<style>
/* index.css 파일 import */
@import "../css/index.css"; /* 추가됨 */
.calendar-container {
  width: 100%;
  max-width: 400px;
  margin: 10px auto;
  text-align: center;
}
.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  font-size: 1.2em;
}
.calendar-days {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 5px;
}
.day-name {
  font-weight: bold;
}
.calendar-date {
  padding: 10px;
  text-align: center;
  background-color: #f0f0f0;
  border-radius: 4px;
}
.calendar-date.is-today {
  background-color: #ffeb3b;
}
.calendar-date.is-selected {
  background-color: #add8e6;
}

.calendar-date.has-note {
  background-color: #ffcccb; /* 메모가 있는 날짜의 배경색 */
}
.note-section {
  margin-top: 20px;
  text-align: center;
}

textarea {
  width: 100%;
  height: 100px;
  margin-top: 10px;
}

.dayBtn {
  padding: 8px 24px; /* 크기 조절 */
  border: none;
  font-size: 18px;
  font-weight: bold;
  background-color: rgb(37, 78, 121); /* 배경색 변경 */
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* 버튼에 그림자 추가 */
}
</style>
