<template>
  <div id="app" class="backgroundContainer">
    <div class="phoneContainer">
      <div class="withBack">
        <p class="pageTitle">전국 노래자랑 안양편 일정</p>
        <img
          role="button"
          tabindex="0"
          alt=""
          @click="[goBack(), stopSpeech()]"
          @keyup="goBack"
          class="backImg"
          src="../assets/뒤로가기.png"
        />
      </div>

      <!-- 공지사항 내용 -->
      <div class="noticeContent" style="margin-top: 20px">
        <!-- 위쪽 여백 추가 -->
        <p class="writtenDate">2024.09.24 작성</p>
        <!-- 작성일 추가 -->
        <h2 style="text-align: center">전국 노래자랑 안양편</h2>
        <p :style="{ fontSize: fontSz + 'px' }">
          <b>일시:</b> 2024년 11월 16일
        </p>
        <p :style="{ fontSize: fontSz + 'px' }">
          <b>참여 대상:</b> 안양시에 거주중인 주민 누구나 가능
        </p>
        <p :style="{ fontSize: fontSz + 'px' }">
          <b>시간:</b> 오후 2시 ~ 오후 7시
        </p>
        <p :style="{ fontSize: fontSz + 'px' }"><b>장소:</b> 안양실내체육관</p>
        <p :style="{ fontSize: fontSz + 'px' }">
          <b>내용:</b> 자세한 사항은 문의 바랍니다.
        </p>
        <div class="buttonGroup1">
          <button class="shortBtn1" @click="upFontSize">+ 확대</button>
          <button class="shortBtn1" @click="downFontSize">- 축소</button>
        </div>
      </div>

      <!-- 일정에 추가하기 버튼 -->
      <div class="buttonContainer">
        <RouterLink to="/main">
          <button class="addToCalendarBtn" @click="stopSpeech">
            일정에 추가하기
          </button>
        </RouterLink>
      </div>
      <div class="btnBundles2">
        <button class="mainBtn b" @click="speakText">
          <img class="mainIcon" src="../assets/재생.png" />
          <span class="mainTxt">재생</span>
        </button>
        <button class="mainBtn b" @click="stopSpeech">
          <img class="mainIcon" src="../assets/정지.png" />
          <span class="mainTxt">정지</span>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "AnPage5",
  components: {},
  data() {
    return {
      fontSz: 16,
      currentTime: null,
      volume: 1.0, // 기본 볼륨
      rate: 1.0, // 기본 속도
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  computed: {},
  methods: {
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    goBack() {
      this.$router.back();
    },
    upFontSize() {
      this.fontSz += 1; // 폰트 크기를 1px 증가
    },
    downFontSize() {
      if (this.fontSz > 1) {
        this.fontSz -= 1; // 폰트 크기를 1px 감소, 최소 1px로 설정
      }
    },
    speakText() {
      const text = `
      전국 노래자랑 안양편
      일시: 2024년 11월 16일
      참여 대상: 안양시에 거주중인 주민 누구나 가능
      시간: 오후 2시 ~ 오후 7시
      장소: 안양실내체육관
      내용: 자세한 사항은 문의 바랍니다
      `;
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.volume = this.volume; // 볼륨 조절
      utterance.rate = this.rate; // 속도 조절
      utterance.lang = "ko-KR"; // 한국어로 설정
      speechSynthesis.speak(utterance);
    },
    stopSpeech() {
      speechSynthesis.cancel(); // 현재 진행 중인 음성을 멈춤
    },
  },
};
</script>

<style>
/* index.css 파일 import */
@import "../css/index.css"; /* 추가됨 */

/* 수정된 부분: 상단바 스타일 */
.topBar {
  display: flex;
  justify-content: center; /* 수평 가운데 정렬 */
  align-items: center;
  margin-top: 10px;
}

.pageTitle {
  font-size: 24px;
  font-weight: bold;
  margin-top: 7px;
  text-align: center;
}
/* 수정 종료 */

/* 공지사항 내용 스타일 */
.noticeContent {
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 7px;
  margin-bottom: 15px; /* 내용 아래 여백 추가 */
  position: relative; /* 부모 요소로부터 상대적 위치 설정 */
}
/* 작성일 스타일 */
.writtenDate {
  position: absolute; /* 부모 요소를 기준으로 위치 설정 */
  top: -15px; /* 내용 위에 위치 */
  right: 10px; /* 부모 요소의 오른쪽에 위치 */
  color: #666; /* 회색으로 설정 */
  font-size: 14px; /* 작은 글꼴 크기 */
}
.noticeContent h2 {
  font-weight: bold;
  margin-bottom: 30px;
}

.noticeContent p {
  margin-top: 20px;
  margin-bottom: 10px;
}

/* 전문 보기 버튼 스타일 */
.viewDetailsBtn {
  margin-top: 20px;
  margin-right: 10px;
  padding: 8px 16px;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}

/* 일정에 추가하기 버튼 스타일 */
.addToCalendarBtn {
  margin-top: 20px; /* 버튼과 내용 사이의 여백 추가 */
  padding: 12px 24px; /* 크기 조절 */
  border: none;
  background-color: #28a745; /* 배경색 변경 */
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}

/* 버튼 컨테이너 스타일 */
.buttonContainer {
  display: flex;
  justify-content: center;
}
</style>
