<template>
  <div class="phoneContainer">
    <!-- 수정된 부분: 상단바 -->
    <div class="withBack">
      <p class="pageTitle">안양시 안내</p>
      <RouterLink to="/main">
        <img
          role="button"
          tabindex="0"
          alt=""
          class="backImg"
          src="../assets/뒤로가기.png"
        />
      </RouterLink>
    </div>
    <!-- 수정 종료 -->
    <!-- 검색 바 -->
    <div class="searchBarContainer">
      <input
        type="text"
        v-model="searchKeyword"
        placeholder="검색어를 입력해주세요"
        class="searchBar"
      />
      <button @click="search" class="searchButton">검색</button>
    </div>
    <!-- 공지사항 게시판 -->
    <div class="noticeBoard">
      <div
        class="noticeItem"
        v-for="(notice, index) in visibleNotices"
        :key="index"
      >
        <!-- An1.vue로 넘어가는 기능 추가 -->
        <p class="noticeTitle" @click="goToAn(notice.title)">
          {{ notice.title }}
        </p>
      </div>
    </div>
    <!-- 게시판 네비게이션 -->
    <div class="boardNavigation">
      <!-- 이전 페이지로 이동 버튼 -->
      <button @click="prevPage">&lt;</button>
      &nbsp;&nbsp;1&nbsp;&nbsp;
      <!-- 다음 페이지로 이동 버튼 -->
      <button @click="nextPage">&gt;</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "AnnounceList",
  components: {},
  data() {
    return {
      currentTime: null,
      currentPage: 1,
      itemsPerPage: 5,
      totalPages: 0,
      searchKeyword: "",
      notices: [
        {
          title: "[건강정보] 만안구 보건소 독감 접종 안내",
        },
        {
          title: "[공지사항] 만안구 안양 2동 공지사항",
        },
        {
          title: "[공사안내] 만안구 배수로 공사 일정",
        },
        {
          title: "[채용공고] 노인 일자리 안내",
        },
        {
          title: "[행사] 전국 노래자랑 안양편 일정",
        },
      ],
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  computed: {
    visibleNotices() {
      const startIndex = (this.currentPage - 1) * this.itemsPerPage;
      const endIndex = startIndex + this.itemsPerPage;
      return this.notices.slice(startIndex, endIndex);
    },
  },
  methods: {
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    goBack() {
      this.$router.back();
    },
    prevPage() {
      this.$router.push({ name: "AnnounceList2" });
    },
    nextPage() {
      this.$router.push({ name: "AnnounceList2" });
    },
    search() {
      console.log("검색어:", this.searchKeyword);
      // 추가적인 검색 로직을 구현할 수 있습니다.
    },
    goToAn(title) {
      if (title === "[건강정보] 만안구 보건소 독감 접종 안내") {
        this.$router.push({ name: "AnPage1" });
      }
      if (title === "[공지사항] 만안구 안양 2동 공지사항") {
        this.$router.push({ name: "AnPage2" });
      }
      if (title === "[공사안내] 만안구 배수로 공사 일정") {
        this.$router.push({ name: "AnPage3" });
      }
      if (title === "[채용공고] 노인 일자리 안내") {
        this.$router.push({ name: "AnPage4" });
      }
      if (title === "[행사] 전국 노래자랑 안양편 일정") {
        this.$router.push({ name: "AnPage5" });
      }
    },
  },
};
</script>

<style>
/* index.css 파일 import */
@import "../css/index.css"; /* 추가됨 */
</style>
