import { createRouter, createWebHistory } from "vue-router";

/* 뷰 추가시 지정 부탁 */
import FirstIn from "../views/FirstIn.vue";
import CreateAccount from "../views/CreateAccount.vue";
import MainDisplay from "../views/MainDisplay.vue";
import CameraFirst from "../views/CameraFirst.vue";
import CameraImage from "../views/CameraImage.vue";
import CameraSummary from "../views/CameraSummary.vue";
import AnnounceList from "../views/AnnounceList.vue";
import AnnounceList2 from "../views/AnnounceList2.vue";

import GalleryDisplay from "../views/GalleryDisplay.vue";
import GallerySummary1 from "../views/GallerySummary1.vue";
import GallerySummary2 from "../views/GallerySummary2.vue";
import GallerySummary3 from "../views/GallerySummary3.vue";
import GallerySummary4 from "../views/GallerySummary4.vue";
import GallerySummary5 from "../views/GallerySummary5.vue";
import GallerySummary6 from "../views/GallerySummary6.vue";

import CalanderPage from "../views/CalanderPage.vue";
import CameraVoice from "../views/CameraVoice.vue";
import An1 from "../views/An1.vue";
import An2 from "../views/An2.vue";
import An3 from "../views/An3.vue";
import An4 from "../views/An4.vue";
import An5 from "../views/An5.vue";

import CameraTest from "../views/CameraTest.vue";

/* 라우터 경로 지정까지 부탁 */
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes: [
    {
      path: "/",
      name: "FirstIn",
      component: FirstIn,
    },

    {
      path: "/cr",
      name: "CreateAccount",
      component: CreateAccount,
    },

    {
      path: "/main",
      name: "MainDisplay",
      component: MainDisplay,
    },

    {
      path: "/cm",
      name: "CameraFirst",
      component: CameraFirst,
    },

    {
      path: "/ci",
      name: "CameraImage",
      component: CameraImage,
    },
    {
      path: "/cs",
      name: "CameraSummary",
      component: CameraSummary,
    },
    {
      path: "/anl",
      name: "AnnounceList",
      component: AnnounceList,
    },
    {
      path: "/anl2",
      name: "AnnounceList2",
      component: AnnounceList2,
    },
    {
      path: "/ct",
      name: "CameraTest",
      component: CameraTest,
    },
    {
      path: "/gl",
      name: "GalleryDisplay",
      component: GalleryDisplay,
    },
    {
      path: "/gs1",
      name: "GallerySummary1",
      component: GallerySummary1,
    },
    {
      path: "/gs2",
      name: "GallerySummary2",
      component: GallerySummary2,
    },
    {
      path: "/gs3",
      name: "GallerySummary3",
      component: GallerySummary3,
    },
    {
      path: "/gs4",
      name: "GallerySummary4",
      component: GallerySummary4,
    },
    {
      path: "/gs5",
      name: "GallerySummary5",
      component: GallerySummary5,
    },
    {
      path: "/gs6",
      name: "GallerySummary6",
      component: GallerySummary6,
    },
    {
      path: "/cv",
      name: "CameraVoice",
      component: CameraVoice,
    },
    {
      path: "/an1",
      name: "AnPage1",
      component: An1,
    },
    {
      path: "/an2",
      name: "AnPage2",
      component: An2,
    },
    {
      path: "/an3",
      name: "AnPage3",
      component: An3,
    },
    {
      path: "/an4",
      name: "AnPage4",
      component: An4,
    },
    {
      path: "/an5",
      name: "AnPage5",
      component: An5,
    },
    {
      path: "/cal",
      name: "CalanderPage",
      component: CalanderPage,
    },
  ],
});

export default router;
