<template>
  <div class="phoneContainer">
    <div class="logoContainer">
      <img class="logoPoorba" src="../assets/푸르바.png" />
    </div>
    <h2 id="firstTitle">전화번호</h2>
    <form @submit.prevent="handleLogin">
      <input
        type="text"
        v-model="formattedPhoneNumber"
        name="username"
        class="inputBar"
        placeholder="전화번호를 입력해주세요"
        @input="formatPhoneNumber"
        maxlength="13"
        required
      />
      <input
        type="submit"
        id="color1"
        class="longCLickBtn"
        value="로그인하기"
      />
    </form>
    <form class="bottomFirst">
      <h3 class="subTitleLeft">회원이 아니신가요?</h3>
      <router-link to="/cr"
        ><input type="submit" class="longCLickBtn" value="회원가입하기"
      /></router-link>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "FirstIn",
  components: {},
  data() {
    return {
      currentTime: null,
      phoneNumber: "", // 전화번호 입력 값을 저장할 데이터
      formattedPhoneNumber: "", // 포맷팅된 전화번호
      validPhoneNumbers: [], // 유효한 전화번호 목록
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
    axios
      .get("/purba1/phoneNumbers.json")
      .then((response) => {
        this.validPhoneNumbers = response.data.phoneNumbers;
      })
      .catch((error) => {
        console.error("전화번호 데이터를 불러오는 중 오류 발생:", error);
      });
  },
  methods: {
    // 전화번호를 입력할 때마다 포맷팅하는 함수
    formatPhoneNumber() {
      // 숫자만 남기기
      this.phoneNumber = this.formattedPhoneNumber.replace(/\D/g, "");

      // 전화번호 형식에 맞게 포맷팅
      if (this.phoneNumber.length < 4) {
        this.formattedPhoneNumber = this.phoneNumber;
      } else if (this.phoneNumber.length < 8) {
        this.formattedPhoneNumber = `${this.phoneNumber.slice(
          0,
          3
        )}-${this.phoneNumber.slice(3)}`;
      } else {
        this.formattedPhoneNumber = `${this.phoneNumber.slice(
          0,
          3
        )}-${this.phoneNumber.slice(3, 7)}-${this.phoneNumber.slice(7)}`;
      }
    },
    handleLogin() {
      // 전화번호 확인 로직
      if (this.validPhoneNumbers.includes(this.formattedPhoneNumber)) {
        // 특정 전화번호일 때 다른 Vue로 전환
        this.$router.push("/main"); // 'next-page'는 이동할 경로로 변경
      } else {
        alert("올바른 전화번호를 입력해주세요.");
      }
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
