<template>
  <div class="phoneContainer">
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="goBack"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
      <h2 class="word1">내용 추출 완료!</h2>
    </div>
    <div class="infDiv">
      <h1 class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        {{ jsonData.title || "제목 없음" }}
      </h1>
      <p class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        <b>날짜:</b> {{ jsonData.start_day || "시작일 없음" }} ~
        {{ jsonData.end_day || "종료일 없음" }}
      </p>
      <p class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        <b>장소:</b> {{ jsonData.location || "장소 정보 없음" }}
      </p>
      <p class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        <b>가격:</b> {{ jsonData.price || "가격 정보 없음" }}
      </p>
      <p class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        <b>시간:</b> {{ jsonData.time || "시간 정보 없음" }}
      </p>
      <p class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        <b>요약:</b> {{ jsonData.summary || "요약 정보 없음" }}
      </p>
      <p class="pre-wrap" :style="{ fontSize: fontSize + 'px' }">
        <b>기타:</b> {{ jsonData.etc || "기타 정보 없음" }}
      </p>
    </div>
    <div class="buttonGroup1">
      <button class="shortBtn1" @click="upFontSize">+ 확대</button>
      <button class="shortBtn1" @click="downFontSize">- 축소</button>
    </div>
    <router-link to="/cv">
      <button class="longBtn1 b">읽어주기</button>
    </router-link>
    <router-link to="/main">
      <button class="longBtn1 b">메인으로</button>
    </router-link>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "CameraSummary",
  components: {},
  data() {
    return {
      jsonData: {},
      currentTime: null,
      fontSize: 16,
    };
  },
  async mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
    // json_output.txt 파일을 axios로 불러오기
    axios
      .get("/purba1/out2.txt") // public 폴더 안의 경로로 요청
      .then((response) => {
        // 텍스트 파일을 파싱해서 jsonData에 저장
        this.jsonData = JSON.parse(response.data);
      })
      .catch((error) => {
        console.error("파일을 불러오는 중 오류 발생:", error);
      });
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    upFontSize() {
      this.fontSize += 1; // 폰트 크기를 1px 증가
    },
    downFontSize() {
      if (this.fontSize > 1) {
        this.fontSize -= 1; // 폰트 크기를 1px 감소, 최소 1px로 설정
      }
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
