<template>
  <div class="phoneContainer">
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="goBack"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
    </div>
    <div>
      <video
        ref="videoElement"
        class="cameraDisplay"
        autoplay
        playsinline
      ></video>
      <br /><br />
      <button class="captureBtn c" @click="toggleCamera">
        전환 (현재: {{ facingMode === "user" ? "전면" : "후면" }})
      </button>
      <router-link to="/ci">
        <button class="captureBtn c" @click="capture">촬영</button>
      </router-link>
      <canvas ref="canvas" style="display: none"></canvas>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onBeforeUnmount } from "vue";
import { useImageStore } from "../stores/imageStore";
import { useRouter } from "vue-router";

export default {
  name: "CameraFirst",
  setup() {
    // 참조 정의
    const videoElement = ref(null);
    const canvas = ref(null);
    const currentStream = ref(null);
    const facingMode = ref("environment"); // 'user'는 전면, 'environment'는 후면
    const imageStore = useImageStore(); // Pinia 스토어 사용
    const router = useRouter();

    /**
     * 카메라 스트림을 가져오는 함수
     */
    const getCameraStream = async () => {
      if (currentStream.value) {
        currentStream.value.getTracks().forEach((track) => track.stop());
      }

      try {
        const constraints = {
          video: {
            facingMode: facingMode.value,
          },
        };
        const stream = await navigator.mediaDevices.getUserMedia(constraints);
        currentStream.value = stream;
        if (videoElement.value) {
          videoElement.value.srcObject = stream;
        }
      } catch (error) {
        console.error("카메라 스트림을 가져오는 중 오류 발생:", error);
        // fallback 처리: facingMode를 제거하여 기본 카메라를 사용
        try {
          const fallbackStream = await navigator.mediaDevices.getUserMedia({
            video: true,
            audio: false,
          });
          currentStream.value = fallbackStream;
          if (videoElement.value) {
            videoElement.value.srcObject = fallbackStream;
          }
        } catch (fallbackError) {
          console.error("Fallback 처리 중 오류 발생:", fallbackError);
          alert("카메라에 접근할 수 없습니다. 권한을 확인해주세요.");
        }
      }
    };

    const goBack = () => {
      router.back();
    };

    /**
     * 전면/후면 카메라를 전환하는 함수
     */
    const toggleCamera = () => {
      facingMode.value = facingMode.value === "user" ? "environment" : "user";
      getCameraStream();
    };

    /**
     * 사진을 캡처하고 저장하며, 서버로 전송하는 함수
     */
    const capture = () => {
      const video = videoElement.value;
      const canvasElement = canvas.value;

      // 캔버스 크기 설정
      canvasElement.width = video.videoWidth;
      canvasElement.height = video.videoHeight;

      // 비디오 프레임을 캔버스에 그리기
      canvasElement
        .getContext("2d")
        .drawImage(video, 0, 0, canvasElement.width, canvasElement.height);

      // 이미지 저장 및 처리
      saveImage(canvasElement);

      // 데이터 URL 생성 및 스토어에 저장
      const imageDataUrl = canvasElement.toDataURL("image/png");
      imageStore.setImageDataUrl(imageDataUrl);

      // 이미지를 서버로 전송
      canvasElement.toBlob((blob) => {
        const formData = new FormData();
        formData.append("image", blob, "purbaCapture.png");

        fetch("http://localhost:8080/upload", {
          // 실제 서버 주소로 수정하세요.
          method: "POST",
          body: formData,
        })
          .then((response) => response.text())
          .then((data) => console.log("서버 응답:", data))
          .catch((error) => console.error("서버 전송 오류:", error));
      });
    };

    /**
     * 이미지를 저장하는 함수
     */
    const saveImage = (canvasElement) => {
      const imageDataUrl = canvasElement.toDataURL("image/png");

      // 다운로드 링크 생성
      const link = document.createElement("a");
      link.href = imageDataUrl;
      link.download = "capture.png"; // 다운로드될 파일의 이름
      document.body.appendChild(link); // 링크를 DOM에 추가
      link.click(); // 링크 클릭 (다운로드 시작)
      document.body.removeChild(link); // 링크 제거
    };

    // 컴포넌트 마운트 시 카메라 스트림 시작
    onMounted(() => {
      getCameraStream();
    });

    // 컴포넌트 언마운트 시 스트림 중지
    onBeforeUnmount(() => {
      if (currentStream.value) {
        currentStream.value.getTracks().forEach((track) => track.stop());
      }
    });

    // 반환할 변수 및 함수
    return {
      videoElement,
      canvas,
      toggleCamera,
      capture,
      facingMode,
      goBack,
    };
  },
};
</script>

<style>
@import "../css/index.css";
</style>
