<template>
  <div class="phoneContainer">
    <div class="modal-overlay" v-if="ModalW == 1">
      <div class="modal-content">
        <img class="imgblk3" src="../assets/img5.jpeg" @click="ModalW = 0" />
      </div>
    </div>
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="[goBack(), stopSpeech()]"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
      <h2 class="word1">2024년 10월 17일</h2>
    </div>
    <div class="imgDisplayed">
      <img class="imgblk2" src="../assets/img5.jpeg" @click="ModalW = 1" />
    </div>
    <div class="infDiv">
      <h1 class="festD" :style="{ fontSize: fontSz + 2 + 'px' }">
        제25회 노인의 날 기념행사 다시.봄 '은평, 다시 어르신을 보다.'
      </h1>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>날짜:</b> 2021년 10월 1일
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>장소:</b> 서울시립은평노인종합복지관 2층 강당
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>가격:</b> 가격 정보 없음
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>시간:</b> 오후 2시
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>요약:</b> 은평구 주최로 제25회 노인의 날을 기념하기 위한 '다시.봄'
        행사가 열립니다. 행사 내용으로는 기념사와 축사, 유공자 표창, 그리고 기념
        영상 상영이 포함되어 있습니다. 또한, 은평구 주관 및 협찬으로 YouTube를
        통해 온라인으로도 시청할 수 있습니다.
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>기타:</b> 행사 당일은 유튜브에서 은평구청을 검색하여 실시간으로
        기념식을 참여할 수 있습니다.
      </p>
      <div class="buttonGroup1">
        <button class="shortBtn1" @click="upFontSize">+ 확대</button>
        <button class="shortBtn1" @click="downFontSize">- 축소</button>
      </div>
    </div>
    <div class="btnBundles2">
      <button class="mainBtn b" @click="speakText">
        <img class="mainIcon" src="../assets/재생.png" />
        <span class="mainTxt">재생</span>
      </button>
      <button class="mainBtn b" @click="stopSpeech">
        <img class="mainIcon" src="../assets/정지.png" />
        <span class="mainTxt">정지</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "GallerySummary5",
  components: {},
  data() {
    return {
      currentTime: null,
      fontSz: 16,
      ModalW: 0,
      volume: 1.0, // 기본 볼륨
      rate: 1.0, // 기본 속도
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    upFontSize() {
      this.fontSz += 1; // 폰트 크기를 1px 증가
    },
    downFontSize() {
      if (this.fontSz > 1) {
        this.fontSz -= 1; // 폰트 크기를 1px 감소, 최소 1px로 설정
      }
    },
    speakText() {
      const text = `
      제25회 노인의 날 기념행사 다시.봄 '은평, 다시 어르신을 보다.',
      날짜: 2021년 10월 1일,
      장소: 서울시립은평노인종합복지관 2층 강당,
      가격: 가격 정보 없음,
      시간: 오후 2시,
      요약: 은평구 주최로 제25회 노인의 날을 기념하기 위한 '다시.봄' 행사가 열립니다. 행사 내용으로는 기념사와 축사, 유공자 표창, 그리고 기념 영상 상영이 포함되어 있습니다. 또한, 은평구 주관 및 협찬으로 YouTube를 통해 온라인으로도 시청할 수 있습니다.,
      기타: 행사 당일은 유튜브에서 은평구청을 검색하여 실시간으로 기념식을 참여할 수 있습니다.
      `;
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.volume = this.volume; // 볼륨 조절
      utterance.rate = this.rate; // 속도 조절
      utterance.lang = "ko-KR"; // 한국어로 설정
      speechSynthesis.speak(utterance);
    },
    stopSpeech() {
      speechSynthesis.cancel(); // 현재 진행 중인 음성을 멈춤
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
