<template>
  <div class="phoneContainer">
    <div class="modal-overlay" v-if="ModalW == 1">
      <div class="modal-content">
        <img class="imgblk3" src="../assets/img4.jpeg" @click="ModalW = 0" />
      </div>
    </div>
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="[goBack(), stopSpeech()]"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
      <h2 class="word1">2024년 10월 17일</h2>
    </div>
    <div class="imgDisplayed">
      <img class="imgblk2" src="../assets/img4.jpeg" @click="ModalW = 1" />
    </div>
    <div class="infDiv">
      <h1 class="festD" :style="{ fontSize: fontSz + 2 + 'px' }">
        2022 노인일자리 및 사회활동 지원사업 참여자 모집
      </h1>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>날짜:</b> 2021.11.29 ~ 2021.12.17
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>장소:</b> 장소 정보 없음
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>가격:</b> 가격 정보 없음
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>시간:</b> 시간 정보 없음
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>요약:</b> 한국노인인력개방원이 2022년 노인일자리 및 사회활동 지원사업
        참여자를 모집합니다. 60~65세 이상의 노인이 참여 가능하며, 공익활동,
        시장형사업단, 사회서비스형 등 다양한 사업에 참여할 수 있습니다.
        모집기간은 2021년 11월 29일부터 12월 17일까지이며, 지역별 행정복지센터나
        노인일자리 수행기관을 방문하거나 온라인으로 지원할 수 있습니다
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>기타:</b> 문의: 1544-3388
      </p>
      <div class="buttonGroup1">
        <button class="shortBtn1" @click="upFontSize">+ 확대</button>
        <button class="shortBtn1" @click="downFontSize">- 축소</button>
      </div>
    </div>
    <div class="btnBundles2">
      <button class="mainBtn b" @click="speakText">
        <img class="mainIcon" src="../assets/재생.png" />
        <span class="mainTxt">재생</span>
      </button>
      <button class="mainBtn b" @click="stopSpeech">
        <img class="mainIcon" src="../assets/정지.png" />
        <span class="mainTxt">정지</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "GallerySummary4",
  components: {},
  data() {
    return {
      currentTime: null,
      fontSz: 16,
      ModalW: 0,
      volume: 1.0, // 기본 볼륨
      rate: 1.0, // 기본 속도
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    upFontSize() {
      this.fontSz += 1; // 폰트 크기를 1px 증가
    },
    downFontSize() {
      if (this.fontSz > 1) {
        this.fontSz -= 1; // 폰트 크기를 1px 감소, 최소 1px로 설정
      }
    },
    speakText() {
      const text = `
      2022 노인일자리 및 사회활동 지원사업 참여자 모집,
      날짜: 2021.11.29 ~ 2021.12.17,
      장소: 장소 정보 없음,
      가격: 가격 정보 없음,
      시간: 시간 정보 없음,
      요약: 한국노인인력개방원이 2022년 노인일자리 및 사회활동 지원사업 참여자를 모집합니다. 60~65세 이상의 노인이 참여 가능하며, 공익활동, 시장형사업단, 사회서비스형 등 다양한 사업에 참여할 수 있습니다. 모집기간은 2021년 11월 29일부터 12월 17일까지이며, 지역별 행정복지센터나 노인일자리 수행기관을 방문하거나 온라인으로 지원할 수 있습니다.,
      기타: 문의: 1544-3388 
      `;
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.volume = this.volume; // 볼륨 조절
      utterance.rate = this.rate; // 속도 조절
      utterance.lang = "ko-KR"; // 한국어로 설정
      speechSynthesis.speak(utterance);
    },
    stopSpeech() {
      speechSynthesis.cancel(); // 현재 진행 중인 음성을 멈춤
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
