<template>
  <div class="phoneContainer">
    <div class="modal-overlay" v-if="ModalW == 1">
      <div class="modal-content">
        <img class="imgblk3" src="../assets/img2.jpeg" @click="ModalW = 0" />
      </div>
    </div>
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="[goBack(), stopSpeech()]"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
      <h2 class="word1">2024년 11월 1일</h2>
    </div>
    <div class="imgDisplayed">
      <img class="imgblk2" src="../assets/img2.jpeg" @click="ModalW = 1" />
    </div>
    <div class="infDiv">
      <h1 class="festD" :style="{ fontSize: fontSz + 2 + 'px' }">
        비대면으로 함께하는 건강걷기대회
      </h1>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>날짜:</b> 2022-10-10 ~ 2022-10-23
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>장소:</b> 광교노인복지관
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>가격:</b> 가격 정보 없음
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>시간:</b> 시간 정보 없음
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>요약:</b> 작년부터 시작된 '건강한 노년' 광고노인복지관의 비대면
        건강걷기대회가 10월 10일부터 23일까지 2주간 진행됩니다. 이번 대회는
        어르신들을 위한 건강키트와 안마기를 포함한 다양한 상품이 준비되어
        있습니다.
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>기타:</b> 참여 방법은 워크온 어플을 설치하고 6만보 걷기대회
        커뮤니티에 가입하여 참여하는 것이며, 자세한 사항은 광고노인복지관
        지역복지팀(031-8006-7424)으로 문의할 수 있습니다.
      </p>
      <div class="buttonGroup1">
        <button class="shortBtn1" @click="upFontSize">+ 확대</button>
        <button class="shortBtn1" @click="downFontSize">- 축소</button>
      </div>
    </div>
    <div class="btnBundles2">
      <button class="mainBtn b" @click="speakText">
        <img class="mainIcon" src="../assets/재생.png" />
        <span class="mainTxt">재생</span>
      </button>
      <button class="mainBtn b" @click="stopSpeech">
        <img class="mainIcon" src="../assets/정지.png" />
        <span class="mainTxt">정지</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "GallerySummary2",
  components: {},
  data() {
    return {
      currentTime: null,
      fontSz: 16,
      ModalW: 0,
      volume: 1.0, // 기본 볼륨
      rate: 1.0, // 기본 속도
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    upFontSize() {
      this.fontSz += 1; // 폰트 크기를 1px 증가
    },
    downFontSize() {
      if (this.fontSz > 1) {
        this.fontSz -= 1; // 폰트 크기를 1px 감소, 최소 1px로 설정
      }
    },
    speakText() {
      const text = `
      비대면으로 함께하는 건강걷기대회,
      날짜: 2022-10-10 ~ 2022-10-23,
      장소: 광교노인복지관,
      가격: 가격 정보 없음,
      시간: 시간 정보 없음,
      요약: 작년부터 시작된 '건강한 노년' 광고노인복지관의 비대면 건강걷기대회가 10월 10일부터 23일까지 2주간 진행됩니다. 이번 대회는 어르신들을 위한 건강키트와 안마기를 포함한 다양한 상품이 준비되어 있습니다.,
      기타: 참여 방법은 워크온 어플을 설치하고 6만보 걷기대회 커뮤니티에 가입하여 참여하는 것이며, 자세한 사항은 광고노인복지관 지역복지팀(031-8006-7424)으로 문의할 수 있습니다.
      `;
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.volume = this.volume; // 볼륨 조절
      utterance.rate = this.rate; // 속도 조절
      utterance.lang = "ko-KR"; // 한국어로 설정
      speechSynthesis.speak(utterance);
    },
    stopSpeech() {
      speechSynthesis.cancel(); // 현재 진행 중인 음성을 멈춤
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
