<template>
  <div class="phoneContainer">
    <div class="modal-overlay" v-if="ModalW == 1">
      <div class="modal-content">
        <img class="imgblk3" src="../assets/img6.jpeg" @click="ModalW = 0" />
      </div>
    </div>
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="[goBack(), stopSpeech()]"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
      <h2 class="word1">2024년 10월 3일</h2>
    </div>
    <div class="imgDisplayed">
      <img class="imgblk2" src="../assets/img6.jpeg" @click="ModalW = 1" />
    </div>
    <div class="infDiv">
      <h1 class="festD" :style="{ fontSize: fontSz + 2 + 'px' }">
        할로윈 페스티벌
      </h1>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>날짜:</b> 2022.10.29
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>장소:</b> 캔바파크 야외운동장
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>가격:</b> 가격 정보 없음
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>시간:</b> 오후 3시 ~ 8시
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>요약:</b> 2022년 10월 29일 토요일 오후 3시부터 8시까지 캔바파크
        야외운동장에서 열리는 할로윈 페스티벌. BOO! +t, 펌킨요리 푸드코트,
        인기가수 특별공연, 좀비들의 퍼레이드, 혈로원 코스톱 대여 등이
        펼쳐집니다.
      </p>
      <p class="festD" :style="{ fontSize: fontSz + 'px' }">
        <b>기타:</b> 캔바파크 야외운동장에서 다채로운 프로그램과 이벤트가
        예정되어 있습니다.
      </p>

      <div class="buttonGroup1">
        <button class="shortBtn1" @click="upFontSize">+ 확대</button>
        <button class="shortBtn1" @click="downFontSize">- 축소</button>
      </div>
    </div>
    <div class="btnBundles2">
      <button class="mainBtn b" @click="speakText">
        <img class="mainIcon" src="../assets/재생.png" />
        <span class="mainTxt">재생</span>
      </button>
      <button class="mainBtn b" @click="stopSpeech">
        <img class="mainIcon" src="../assets/정지.png" />
        <span class="mainTxt">정지</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "GallerySummary6",
  components: {},
  data() {
    return {
      currentTime: null,
      fontSz: 16,
      ModalW: 0,
      volume: 1.0, // 기본 볼륨
      rate: 1.0, // 기본 속도
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    upFontSize() {
      this.fontSz += 1; // 폰트 크기를 1px 증가
    },
    downFontSize() {
      if (this.fontSz > 1) {
        this.fontSz -= 1; // 폰트 크기를 1px 감소, 최소 1px로 설정
      }
    },
    speakText() {
      const text = `
      할로윈 페스티벌,
      날짜: 2022.10.29,
      장소: 캔바파크 야외운동장,
      가격: 가격 정보 없음,
      시간: 오후 3시 ~ 8시,
      요약: 2022년 10월 29일 토요일 오후 3시부터 8시까지 캔바파크 야외운동장에서 열리는 할로윈 페스티벌. BOO! +t, 펌킨요리 푸드코트, 인기가수 특별공연, 좀비들의 퍼레이드, 혈로원 코스톱 대여 등이 펼쳐집니다.,
      기타: 캔바파크 야외운동장에서 다채로운 프로그램과 이벤트가 예정되어 있습니다. 
      `;
      const utterance = new SpeechSynthesisUtterance(text);
      utterance.volume = this.volume; // 볼륨 조절
      utterance.rate = this.rate; // 속도 조절
      utterance.lang = "ko-KR"; // 한국어로 설정
      speechSynthesis.speak(utterance);
    },
    stopSpeech() {
      speechSynthesis.cancel(); // 현재 진행 중인 음성을 멈춤
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
