<template>
  <div class="phoneContainer">
    <div class="loading-modal" v-if="isLoading == 1">
      <div class="loading-content">
        <!-- 공룡 이미지 -->
        <img :src="currentImg" alt="Dinosaur running" class="dino-image" />

        <!-- 프로그레스 바 -->
        <div class="progress-bar">
          <div class="progress-fill" :style="{ width: progress + '%' }"></div>
        </div>

        <h2>잠시만 기다려주세요..</h2>
      </div>
    </div>
    <div class="modalBack" v-if="ModalWindow1 == 1">
      <div class="modalWindow">
        <h2 class="word2">무엇을 하시겠습니까?</h2>
        <div class="modalGroup">
          <button class="modalBtn" @click="vueClick('CameraSummary')">
            요약
          </button>
          <button class="modalBtn" @click="vueClick('CameraVoice')">
            읽어주기
          </button>
        </div>
      </div>
    </div>
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="goBack"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
      <h2 class="word1">캡처 화면</h2>
    </div>
    <div class="captureContainer">
      <img
        v-if="imageDataUrl"
        :src="imageDataUrl"
        alt="캡처된 이미지"
        class="captureDisplay"
      />
    </div>
    <div class="buttonGroup1">
      <router-link to="/cm">
        <button class="captureBtn2">재촬영</button>
      </router-link>
      <button @click="ModalWindow1 = 1" class="captureBtn2">완료</button>
    </div>
  </div>
</template>

<script>
import { useImageStore } from "../stores/imageStore";

export default {
  name: "CameraImage",
  components: {},
  data() {
    return {
      currentTime: null,
      imageDataUrl: useImageStore().imageDataUrl,
      ModalWindow1: 0,
      isLoading: 0,
      progress: 0, // 프로그레스 상태
      dinoImages: [
        require("@/assets/dinorun1.png"), // 첫 번째 공룡 이미지
        require("@/assets/dinorun2.png"), // 두 번째 공룡 이미지
      ],
      currentImg: require("@/assets/dinorun1.png"),
      currentIdx: 0,
    };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  methods: {
    vueClick(vueName) {
      this.isLoading = 1;
      this.ModalWindow1 = 0;
      this.startLoading();
      this.startImageRotation(); // 이미지 전환 시작

      // 10초 후에 다른 페이지로 이동
      setTimeout(() => {
        this.isLoading = 0;
        this.$router.push({ name: vueName }); // 다른 컴포넌트로 이동
      }, 22000); //22초
    },
    startLoading() {
      this.progress = 0;
      const interval = setInterval(() => {
        if (this.progress < 100) {
          this.progress += 100 / 22; // 1%씩 증가
        } else {
          clearInterval(interval);
        }
      }, 1000); // 1초에 10%씩, 총 10초 동안 100%로 채워짐
    },
    startImageRotation() {
      this.imageInterval = setInterval(() => {
        this.currentIdx = (this.currentIdx + 1) % 2;
        this.currentImg = this.dinoImages[this.currentIdx]; // 이미지를 번갈아 변경
      }, 1000); // 1초마다 이미지 변경
    },
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
  },
  beforeUnmount() {
    clearInterval(this.imageInterval); // 컴포넌트가 파괴되기 전에 이미지 인터벌을 정리
  },
};
</script>

<style>
@import "../css/index.css";
</style>
